.appendix {
  margin-top: 2rem;
}

.aural {
  height: 1px;
  overflow: hidden;
  position: absolute;
  top: -1000px;
  width: 1px;
  margin: 0 !important;
  padding: 0 !important;
}

body,
body,
body {
  margin: 0;
  background-color: #FFFFFF;
  font-size: 1.125rem;
  line-height: 1.66667;
}

.js-easypopup {
  position: relative;
  z-index: auto;
}

.js-easypopup .js-easypopup__toggler {
  cursor: pointer;
}

.js-easypopup .js-easypopup__content {
  display: none;
  position: absolute;
  z-index: 1;
  max-width: 90vw;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.4);
  padding: 0.75rem;
  background-color: #f2f2f2;
  border: 1px solid #CCCCCC;
}

.js-easypopup .js-easypopup__content form {
  background: transparent;
  padding: 0;
}

.js-easypopup .js-easypopup__close {
  position: absolute;
  top: 0.5em;
  right: 0.5em;
}

.js-easypopup.opened .js-easypopup__content {
  display: block;
}

