#footer {
  padding: 1.25rem 40px;
  padding-top: 1.25rem;
  background-color: #FFFFFF;
  text-align: left;
  color: #000000;
  text-align: center;
  margin-top: 40px;
}

#footer .logo {
  width: 70px;
}

#footer .heading {
  font-size: 1.2em;
  font-weight: 700;
  display: block;
  margin-top: 20px;
  margin-bottom: 0.4em;
}

#footer .author {
  color: #086D9A;
  display: block;
  margin-bottom: 0.6em;
}

#footer .copyright {
  color: #aaaaaa;
  display: block;
  margin-bottom: 0.6em;
}

@media screen and (max-width: 45rem) {
  #footer.hide {
    display: none;
  }
}

#header,
#header {
  position: relative;
  background-color: #FFFFFF;
}

@media screen and (max-width: 45rem) {
  #header.fixed,
  #header.fixed {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100vw;
    height: 105vh;
    overflow-y: scroll;
  }
}

.page-anzeige {
  font-size: 0.6875rem;
  color: #999999;
  float: right;
  margin: 0.6em 0;
  text-transform: uppercase;
}

.portal-head {
  padding-top: 0.625rem;
  padding-bottom: 0.625rem;
  padding-left: 2.5rem;
  padding-right: 2.5rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  margin: 0;
  background-color: #FFFFFF;
}

.portal-head:after {
  content: "";
  display: table;
  clear: both;
}

@media screen and (max-width: 45rem) {
  .portal-head {
    padding: 0.75rem;
    padding-bottom: 0.375rem;
    padding-left: 1.25rem;
    padding-right: 1.25rem;
  }

  .portal-head .portal-logo {
    width: 7.6875rem;
  }

  .portal-head .portal-claim {
    display: none;
  }

  .portal-head .portal-meta {
    display: none;
  }
}

.portal-head .portal-logo {
  flex-grow: 1;
}

.portal-head .meta {
  flex-grow: 0;
  display: inline-block;
  position: relative;
  display: flex;
  align-items: center;
  float: right;
}

.portal-head .meta .fa {
  color: #000000;
  font-size: 1.3em;
}

.portal-head .meta .fa.fa-shopping-cart {
  font-size: 1.8em;
}

.portal-head .meta > * {
  padding: 0.5em;
  margin-left: 15px;
}

.portal-head__cart {
  position: relative;
}

.portal-head__cart .counter {
  position: absolute;
  background: #E71835;
  color: #FFFFFF;
  top: 0;
  right: 0;
  border-radius: 50%;
  min-width: 1.8em;
  min-height: 1.5em;
  text-align: center;
  vertical-align: middle;
  font-size: 0.7em;
}

.portal-head__hamburger {
  min-width: 2.2em;
  text-align: center;
  text-decoration: none;
}

.portal-head__hamburger:hover,
.portal-head__hamburger:focus {
  color: #086D9A;
  background-color: transparent;
  text-decoration: none;
}

.portal-head__hamburger.opened .hamburger__icon::before {
  content: "\f00d";
}

.portal-head__hamburger:hover {
  text-decoration: none;
}

